import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import './styles/main.css';

// Fix service worker registration
if ('serviceWorker' in navigator) {
  window.addEventListener('load', () => {
    // Get the GitHub Pages base path
    const isGitHubPages = window.location.hostname.includes('github.io');
    const basePath = isGitHubPages ? '/mindmatters-app' : '';
    
    // Register only one service worker with the correct path
    const swUrl = `${basePath}/service-worker.js`;
    
    console.log('Registering service worker at:', swUrl);
    navigator.serviceWorker.register(swUrl)
      .then(registration => {
        console.log('ServiceWorker registered with scope:', registration.scope);
      })
      .catch(error => {
        console.error('ServiceWorker registration failed:', error);
        // App continues to function even without the service worker
      });
  });
}

// This will hold the deferred prompt for installation
window.deferredPrompt = null;

// Listen for the beforeinstallprompt event
window.addEventListener('beforeinstallprompt', (e) => {
  // Prevent Chrome 67+ from automatically showing the prompt
  e.preventDefault();
  
  // Stash the event so it can be triggered later
  window.deferredPrompt = e;
  
  console.log('App can be installed');
});

// Listen for app installed event
window.addEventListener('appinstalled', () => {
  // Clear the deferredPrompt so it can't be used again
  window.deferredPrompt = null;
  console.log('PWA installed successfully');
});

// Handle offline status initially
if (!navigator.onLine) {
  document.body.classList.add('offline-mode');
}

// Listen for online/offline events
window.addEventListener('online', () => document.body.classList.remove('offline-mode'));
window.addEventListener('offline', () => document.body.classList.add('offline-mode'));

ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
);